<template>
  <div class="customer__add__box margin-top-20 margin-bottom-30">
    <div class="margin-top-20 step-box" v-if="showStep">
      <Step :activeStep="2"></Step>
    </div>
    <el-row :gutter="24" class="margin-top-20">
      <el-col :span="11" class="add-form-col">
        <h4 class="margin-bottom-30">客户基本信息：</h4>
        <AddForm
          :customerData="formData"
          :pageType="pageType"
          ref="customerForm"
          @submitFun="submitFun"
        ></AddForm>
      </el-col>
      <div class="divider"></div>
      <el-col :span="10" class="baby-form-col">
        <h4 class="margin-bottom-30">宝宝信息：</h4>
        <BabyForm
          ref="babyForm"
          :babyArr="babyArr"
          @removeBaby="removeBaby"
        ></BabyForm>
      </el-col>
    </el-row>
    <Dialog
      v-model="showDialog"
      :showCancelBtn="true"
      :btnText="'确认'"
      :title="''"
      @btnHandle="dialogConfirm"
    >
      <template slot="headTip">
        确认修改客户信息提示
      </template>
      <template slot="content">
        确认修改该客户信息吗？
      </template>
    </Dialog>
  </div>
</template>
<script>
import AddForm from './components/AddForm'
import BabyForm from './components/BabyForm'
import Dialog from '@/components/Dialog'

import Step from './components/Step'
import {
  addCustomer as addCustomerApi,
  updateCustomer,
  getRelatedInfoById,
  getCustomerInfoById,
  customerRelatedUser
} from '@/api/crmManage'
import _ from 'lodash'
export default {
  data() {
    return {
      babyFormValid: true, //baby表单验证
      relatedData: {},
      cusertomerData: {},
      formData: {},
      addData: {},
      userId: '',
      showDialog: false,
      babyArr: [],
      relativeId: ''
    }
  },
  props: {
    showStep: {
      type: Boolean,
      default: true
    },
    pageType: {
      type: String,
      default: ''
    }
  },
  components: {
    AddForm,
    Step,
    BabyForm,
    Dialog
  },
  created() {
    this.relativeId = this.$route.query.relative_id
    this.userId =
      this.pageType === 'updateCustomer'
        ? this.$route.params.id
        : this.$route.query.userId
  },
  watch: {
    relativeId(val) {
      if (val) {
        this.getData()
      }
    },
    userId() {
      if (!this.relativeId) {
        this.getData()
      }
    }
  },
  methods: {
    removeBaby(parentInd) {
      this.babyArr = _.remove(this.babyArr, function(n, index) {
        return !(index === parentInd)
      })
    },
    async getRelatedInfoById() {
      if (this.relativeId) {
        await getRelatedInfoById(this.relativeId).then(res => {
          if (res) {
            this.relatedData = res.data
            this.babyArr = res.data.babys
          }
        })
      }
    },
    async getData() {
      await this.getRelatedInfoById()
      if (this.userId) {
        await getCustomerInfoById(this.userId).then(res => {
          if (res && res.data) {
            this.cusertomerData = res.data
            this.babyArr = this.babyArr.concat(res.data.babys)
          }
        })
      }
      let data = Object.assign(this.cusertomerData, this.relatedData)
      data.sourceChannel = data.source_channel ? data.source_channel : ''
      this.formData = data
    },
    submitFun(val) {
      const babyFormRef = this.$refs.babyForm.$refs
      const that = this
      _.forIn(babyFormRef, function(babyItem, key) {
        if (babyItem && babyItem.length && babyItem[0].disabled !== true) {
          babyItem[0].validate(childValid => {
            if (childValid) {
              that.babyFormValid = true
            } else {
              that.babyFormValid = false
              return false
            }
          })
        } else {
          that.babyFormValid = true
        }
      })
      if (!this.babyFormValid) {
        return
      }
      this.addData = val
      this.addData.source_channel = this.addData.sourceChannel
        ? this.addData.sourceChannel[2]
        : ''
      if (this.babyArr.length) {
        // 宝宝数据处理，只返回可编辑的录入的宝宝，不需要传关联的宝宝
        var removeBabyArr = _.filter(this.babyArr, function(n) {
          return n.can_edit
        })
        // 如果没有填写宝宝姓名，给默认值未知
        removeBabyArr = removeBabyArr.map(item => {
          return {
            ...item,
            name: item.name ? item.name : '未知'
          }
        })
        this.addData.babys = removeBabyArr
      } else {
        this.addData.babys = []
      }
      this.addData.relative_user = this.relativeId
      // 修改信息确认弹窗
      if (this.pageType === 'updateCustomer') {
        this.showDialog = true
        return
      }
      this.submitApiFun()
    },
    submitApiFun() {
      if (this.babyFormValid && this.userId && this.relativeId) {
        this.customerRelatedUser(this.addData, this.userId) //客户关联用户
      } else if (this.babyFormValid) {
        this.addCustomerFun(this.addData) //修改或添加客户信息
      }
    },
    dialogCancel() {
      this.showDialog = false
    },
    dialogConfirm() {
      this.submitApiFun()
      this.dialogCancel()
    },
    addCustomerFun(data) {
      const apiName = this.userId ? updateCustomer : addCustomerApi
      const opratorName = this.userId ? '修改' : '添加'
      apiName(data, this.userId).then(res => {
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result ? opratorName + '成功' : opratorName + '失败'
        })
        const data = this.initCustomerForm()
        this.$refs.customerForm.formData = data
        if (res && res.data.result) {
          this.$router.push({
            path: `${this.$route.meta.activeMenu}`,
            query: {
              date: new Date().getTime()
            }
          })
        }
      })
    },
    customerRelatedUser(params, id) {
      customerRelatedUser(params, id).then(res => {
        const opratorName = '关联用户'
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result ? opratorName + '成功' : opratorName + '失败'
        })
        const data = this.initCustomerForm()
        this.$refs.customerForm.formData = data
        if (res && res.data.result) {
          this.$router.push({
            path: `${this.$route.meta.activeMenu}`,
            query: {
              date: new Date().getTime()
            }
          })
        }
      })
    },
    initCustomerForm() {
      const formData = {
        nickname: '',
        weapp_id: '',
        is_during_pregnancy: '',
        user_type: '',
        source_channel: '',
        customer_ask: '',
        realName: '',
        relative_user: '',
        phone: '',
        remark: '',
        sourceChannel: ''
      }
      return formData
    }
  }
}
</script>

<style lang="scss">
.customer__add__box {
  .step-box {
    margin: 0 auto;
    width: 50%;
  }
  .add-form-col {
    margin-right: 80px;
    .btn-group {
      float: right;
      margin-right: -120px;
      text-align: center;
    }
  }
  .divider {
    float: left;
    margin-right: 10px !important;
    margin-left: -25px;
    padding-right: 10px !important;
    width: 1px;
    height: 100%;
    min-height: 520px;
    border-right: 1px solid #ebeef5;
  }
  .baby-form-col {
    margin-left: 40px;
  }
}
</style>
