<template>
  <MyForm
    :form="form"
    ref="addCustomerForm"
    :formData="formData"
    :formItem="formItem"
    @submit="searchSubmit"
  ></MyForm>
</template>
<script>
import { getFormField } from '../../../getFiled'
import MyForm from '@/components/MyForm'
import _ from 'lodash'
export default {
  data() {
    // 表单配置
    var addCustomerForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'addCustomerForm',
      labelWidth: '200px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      btnRight: true,
      userId: '',
      relativeId: '',
      cancleShow: false,
      submitText: '确认提交',
      cancleText: ''
    }
    return {
      searchParams: {},
      formItem: [],
      formData: {},
      form: addCustomerForm
    }
  },
  props: {
    customerData: {
      type: Object,
      default: () => {}
    },
    optionsVal: {
      type: String,
      default: ''
    },
    moduleName: {
      type: String,
      default: ''
    },
    pageType: {
      type: String,
      default: ''
    }
  },
  components: {
    MyForm
  },
  created() {
    this.userId = this.$route.params.id || this.$route.query.userId
    this.relativeId = this.$route.query.relative_id
  },
  watch: {
    customerData: {
      handler(val) {
        this.formInit(val)
      },
      deep: true
    }
  },
  methods: {
    setFormAttrByPage() {
      if (this.userId) {
        this.form.submitText = '确认修改'
        this.formItem[3].isOpen = 'all'
        this.formItem[4].isOpen = 'all'
        this.formItem[3].disabled = true
        this.formItem[4].disabled = true
      } else {
        this.form.submitText = '确认提交'
        this.formItem[3].isOpen = true
        this.formItem[4].isOpen = true
        this.formItem[3].disabled = false
        this.formItem[4].disabled = false
      }
      this.formItem[0].disabled = this.userId || this.relativeId ? true : false
      this.formItem[5].disabled = this.userId ? true : false
    },
    searchSubmit(val) {
      this.$emit('submitFun', val)
    },
    formInit(data) {
      this.formItem = getFormField('customerAddForm', 'item')
      const hasData = _.some(data, Boolean)
      this.formData = hasData ? data : getFormField('customerAddForm', 'data')
      this.setFormAttrByPage()
    },
    reset() {
      this.$emit('reset')
    }
  }
}
</script>
