<template>
  <div class="baby_answer_box">
    <div v-if="babyArr.length">
      <el-row
        v-for="(item, index) in babyArr"
        :key="index + 'baby'"
        :gutter="24"
      >
        <div class="baby_form_box">
          <div class="baby_form_item">
            <i
              v-show="item.can_edit !== false"
              class="el-icon-close close_baby_icon"
              @click="removeBaby(index)"
            />
            <el-form
              :ref="'babyArr' + index"
              :model="item"
              :disabled="item.can_edit === false"
              label-width="120px"
              label-position="right"
              class="baby_answer_Form"
              size="mini"
            >
              <el-form-item label="宝宝乳名" prop="name">
                <el-input v-model="item.name" placeholder="请输入宝宝乳名" />
              </el-form-item>
              <el-form-item
                label="宝宝是否出生"
                prop="is_born"
                :rules="{
                  required: true,
                  message: '请选择宝宝是否出生',
                  trigger: 'blur'
                }"
              >
                <el-radio-group v-model="item.is_born">
                  <el-radio :label="true">已出生</el-radio>
                  <el-radio :label="false">未出生</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item
                v-if="item.is_born === true"
                label="宝宝性别"
                prop="gender"
                :rules="{
                  required: true,
                  message: '请选择宝宝性别',
                  trigger: 'blur'
                }"
              >
                <el-radio-group v-model="item.gender">
                  <el-radio :label="1">男</el-radio>
                  <el-radio :label="2">女</el-radio>
                  <el-radio :label="0">未知</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item
                v-if="item.is_born === true"
                label="宝宝出生日期"
                prop="birthday"
                :rules="{
                  required: true,
                  message: '请选择宝宝出生日期',
                  trigger: 'blur'
                }"
              >
                <el-date-picker
                  placeholder="请选择宝宝出生日期"
                  type="date"
                  :picker-options="pickerOptions"
                  :disabled="item.disabled"
                  v-model="item.birthday"
                ></el-date-picker>
              </el-form-item>
              <el-form-item
                v-if="item.is_born === false"
                label="宝宝预产期"
                prop="birthday"
                :rules="{
                  required: true,
                  message: '请选择宝宝预产期',
                  trigger: 'blur'
                }"
              >
                <el-date-picker
                  placeholder="请选择宝宝预产期"
                  type="date"
                  :disabled="item.disabled"
                  v-model="item.birthday"
                  :picker-options="noBornOptions"
                ></el-date-picker>
              </el-form-item>
              <el-form-item
                label="宝宝月龄"
                v-if="item.age_of_month"
                prop="age_of_month"
              >
                <el-input
                  v-model="item.age_of_month"
                  :disabled="true"
                  placeholder="请填写宝宝月龄"
                />
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-row>
    </div>
    <div>
      <p
        class="add_answer_btn"
        style="margin:0 auto;width:200px;"
        @click="addBabyFun()"
      >
        <i class="el-icon-plus" />添加宝宝
      </p>
    </div>
  </div>
</template>
<script>
import { getRandom } from '@/filters'
export default {
  props: {
    babyArr: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      noBornOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now()
        }
      }
    }
  },
  components: {},
  created() {},
  methods: {
    getRandom,
    // setFormAttrByPage() {
    //   if (this.userId) {
    //     this.form.submitText = '确认修改'
    //     this.formItem[3].isOpen = 'all'
    //     this.formItem[4].isOpen = 'all'
    //     this.formItem[3].disabled = true
    //     this.formItem[4].disabled = true
    //   } else {
    //     this.form.submitText = '确认提交'
    //     this.formItem[3].isOpen = true
    //     this.formItem[4].isOpen = true
    //     this.formItem[3].disabled = false
    //     this.formItem[4].disabled = false
    //   }
    // },
    formInit(row, index) {
      const babyInfo = {
        name: '',
        birthday: '',
        is_born: '',
        gender: '',
        can_edit: true,
        age_of_month: ''
      }
      this.babyArr.push(babyInfo)
    },
    addBabyFun() {
      this.formInit()
    },
    removeBaby(parentInd) {
      this.$emit('removeBaby', parentInd)
    }
  }
}
</script>
<style lang="scss">
.baby_answer_box {
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0 auto;
  margin-right: 40px;
  width: 96%;
  max-height: 478px;
  .baby_form_box {
    padding: 20px 30px 20px;
  }
  .baby_form_item {
    position: relative;
    padding: 15px 15px 0;
    background: #f5f7fa;
    border: 1px solid #e8eaec;
    border-radius: 4px;
  }
  .close_baby_icon {
    position: absolute;
    top: -10px;
    right: -10px;
    padding: 2px;
    color: #fff;
    background: #f8747f;
    border-radius: 50%;
    cursor: pointer;
  }
  p {
    text-align: center;
    margin-bottom: 20px;
  }
  .el-form--label-top .el-form-item__label {
    width: 100%;
    text-align: center;
  }
  .width80 {
    width: 85%;
  }
  .del_btn {
    margin-left: 15px;
    font-size: 16px;
    color: #576277;
    border-radius: 4px;
    cursor: pointer;
  }
  .answer_box {
    .el-form-item {
      margin-bottom: 15px;
    }
  }
  .add_answer_btn {
    padding: 10px 0;
    text-align: center;
    color: #0085ff;
    background: #fff;
    border: 1px dotted #0085ff;
    border-radius: 4px;
    cursor: pointer;
  }
}
</style>
